<template>
  <div :class="style.layout">
      <div :class="style.textCommon"><b>wow</b><span>哇哦！</span></div>
      <div :class="style.titlelist">  
        <ul v-if="hidebanner" :class="style.showhead">
          <li v-for="v in headerdata" v-if="v.id!='6'&&v.id!='7'" :class="active==v.id?style.activeaa:''" @click="showBanner(v.id)">{{v.name}}</li>
        </ul>
        <ul :class="style.titlert" v-if="hidebanner ">
              <li :class="active=='6'?style.activeaa:''" @click="showBanner('6')">空杯学 ...</li>
            <li :class="active=='7'?style.activeaa:''" @click="showBanner('7')">千里寻...</li>
          </ul> 
        <div :class="style.titleicon" v-if="!hidebanner" @click="showHeader"><i :class="style.menu" ></i></div> 
      </div>
    <ul v-if="!hidebanner && !mini"  :class="style.hidehead">
          <li v-for="v in headerdata" :class="active==v.id?style.active:''" @click="showBanner(v.id)">{{v.name}}</li>
    </ul>
    <div v-show="isbanner" :class="style.banner">
      <ul>
        <li v-for="v in bannerdata.lxqd" :class="list==v.id?style.activebb:''" :id="v.id" :key="v.id" @click="getDetail(v.id)">{{v.name}}</li>
      </ul>
    </div>
  </div>
    
</template>
<script>

import style from '@/css/header.css'
import headerbanner from '@/data/header-banner.json'
import { SET_HASLOINGED } from '../modules/login/mutation-types'
import { mapMutations, mapActions,mapGetters } from 'vuex'
import { exitSession, user } from '@/components/modules/login/mutation-types'

export default {
  name: 'app-header',
  data() {
    return {
      style,
      isbanner:false,
      username: '超级用户',
      date: new Date(),
      isbanner:false,
      headerdata:[
        {name:'首页',id:'0'},
        {name:'乐享前端',id:'1'},
        {name:'漫谈设计',id:'2'},
        {name:'新媒体思维',id:'3'},
        {name:'智能科技',id:'4'},
        {name:'悠闲生活',id:'5'},
        {name:'空杯学',id:'6'},
        {name:'千里寻',id:'7'},
      ],
      bannerdata:headerbanner,
      hidebanner:true,
      widthbox:'',
      mini:true,
      active:'',
      list:''
      
    }
  },
  computed: {
    dateStr() {
      let year = this.date.getFullYear();
      let month = this.date.getMonth() + 1;
      let day = this.date.getDate();
      let hour = this.date.getHours();
      let minitue = this.date.getMinutes();
      let second = this.date.getSeconds();
      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        day = "0" + day;
      }
      if (hour < 10) {
        hour = "0" + hour;
      }
      if (minitue < 10) {
        minitue = "0" + minitue;
      }
      if (second < 10) {
        second = "0" + second;
      }
      return `${year}-${month}-${day} ${hour}:${minitue}:${second}`
    },
    ...mapGetters({
      user
    })
  },
  methods: {
    ...mapActions({
      exitSession
    }),
    ...mapMutations({
      SET_HASLOINGED
    }),
      showHeader(){
          this.hidebanner=false;
          this.isbanner=false;
          this.mini=!this.mini 
    },
    showBanner(id){  //显示二级导航
      this.isbanner=true;
      this.active=id;

    },
    getDetail(id){
      this.list=id
    },
    exit() {
      this.exitSession();
      this.SET_HASLOINGED(false);
      this.$router.push({ name: "login" });
    },
    autoSize(){
        this.widthbox=$(window).width();
        if(this.widthbox<=850){
          this.isbanner=false;
          this.hidebanner=false
          }else{
            this.hidebanner=true;
          }; 
        }
  },

  created() {
    // this.bannerdata=headerbanner;
  },
  mounted() {
      this.autoSize();
      $(window).newresize(this.autoSize,1);

  }
}
</script>


