<template>
    <div :class="style.backcolor">
        <div id="mydiv" :class="style.convas"></div>
        <log-form :class="style.form" ref="form"></log-form>
        <div :class="style.footer">
          <div :class="style.backpictrue" id="showback">
                <a :class="style.space" href="http://www.wowgoo.cn">静待花开的前端大计</a>
          </div>
        </div>
    </div>
</template>
<script>
import CanvasParticle from '../../../../static/libs/canvas-particle.js'
import style from '@/css/login.css'
import {login,getSession,SET_USER} from './mutation-types'
import {mapActions} from 'vuex'
import LogForm from './formrouter'
export default {
   name:'login',
   data(){
       return {
           style,
           // showcanvas:false,
           config:{
            vx: 4,  //小球x轴速度,正为右，负为左
            vy: 4,  //小球y轴速度
            height: 2,  //小球高宽，其实为正方形，所以不宜太大
            width: 2,
            count: 500,   //点个数
            color: "121, 162, 185",   //点颜色
            stroke: "130,255,255",    //线条颜色
            dist: 6000,   //点吸附距离
            e_dist: 20000,  //鼠标吸附加速距离
            max_conn: 10 //点到点最大连接数
           }
       };       
   },
   components:{
       LogForm
   },
   methods:{
       // ...mapActions({
       //     login,
       //     getSessions
       // }),
       // showBack(){
       //  setTimeout(()=>{
          
       //  },'slow')
       // }
   },
   created(){
        // this.showcanvas=true;
   },
   mounted(){
    console.log(this.$router.history.current.name);
      
       //调用
       // this.showBack();
        CanvasParticle(this.config);
        // var that=this;
        // $(window).resize(function(){
        //   CanvasParticle(that.config);
        // })     

   },
   destroyed(){
    this.showcanvas=false;
    let canvas = $('#mydiv');  //选择要擦除的canvas元素
    canvas.attr('width', 0);
    canvas.attr('height', 0);
   }
}
</script>




