import Vue from 'vue'
import Router from 'vue-router'
import routeResolve from '@/util/route-resolving'
import login from '#mod/login/router'
import main from '#mod/main/router'
Vue.use(Router);
export default new Router({
  base: __dirname,
  redirect:{name:'login'},
  routes: routeResolve({
    subRoutes: [login,main]
  }).children
})



