L.TileLayer.WMTS = L.TileLayer.extend({

    defaultWmtsParams: {
        service: 'WMTS',
        request: 'GetTile',
        version: '1.0.0',
        layer: '',
        style: '',
        tilematrixSet: '',
        format: 'image/jpeg',
        transparent: false
    },

    options: {
        crs: null,
        uppercase: false,
        noWrap: true,
    },

    initialize: function(url, options) { // (String, Object)
        this._url = url;
        var wmtsParams = L.extend({}, this.defaultWmtsParams);
        for (var i in options) {
            if (!(i in this.options)) {
                wmtsParams[i] = options[i];
            }
        }

        options = L.setOptions(this, options);

        wmtsParams.width = wmtsParams.height = options.tileSize * (options.detectRetina && L.Browser.retina ? 2 : 1);

        this.wmtsParams = wmtsParams;
        this.matrixIds = options.matrixIds || this.getDefaultMatrix();
    },

    onAdd: function(map) {
        this._crs = this.options.crs || map.options.crs;
        this._wmtsVersion = parseFloat(this.wmtsParams.version);

        var projectionKey = this._wmtsVersion >= 1.3 ? 'crs' : 'srs';
        this.wmtsParams[projectionKey] = this._crs.code;

        L.TileLayer.prototype.onAdd.call(this, map);
    },

    getTileUrl: function(coords) {

        var tileBounds = this._tileCoordsToBounds(coords),
            nw = this._crs.project(tileBounds.getNorthWest()),
            se = this._crs.project(tileBounds.getSouthEast());
        var zoom = coords.z + 1;
        var tilewidth = se.x - nw.x;
        var ident = this.matrixIds[zoom].identifier;
        var X0 = this.matrixIds[zoom].topLeftCorner.lng;
        var Y0 = this.matrixIds[zoom].topLeftCorner.lat;
        var tilecol = Math.floor((nw.x - X0) / tilewidth);
        var tilerow = -Math.floor((nw.y - Y0) / tilewidth);

        var url = L.TileLayer.prototype.getTileUrl.call(this, coords);
        return url + L.Util.getParamString(this.wmtsParams, url, this.options.uppercase) + "&tilematrix=" + ident + "&tilerow=" + tilerow + "&tilecol=" + tilecol;
    },

    //  getTileUrl: function (tilePoint, zoom) { // (Point, Number) -> String
    //      var map = this._map;
    //      crs = map.options.crs;
    //      tileSize = this.options.tileSize;
    //      nwPoint = tilePoint.multiplyBy(tileSize);
    //      //+/-1 in order to be on the tile
    //      nwPoint.x+=1;
    //      nwPoint.y-=1;
    //      sePoint = nwPoint.add(new L.Point(tileSize, tileSize));
    //      nw = crs.project(map.unproject(nwPoint, zoom));
    //      se = crs.project(map.unproject(sePoint, zoom));
    //      tilewidth = se.x-nw.x;
    //      zoom=map.getZoom();
    //      ident = this.matrixIds[zoom].identifier;
    //      X0 = this.matrixIds[zoom].topLeftCorner.lng;
    //      Y0 = this.matrixIds[zoom].topLeftCorner.lat;
    //      tilecol=Math.floor((nw.x-X0)/tilewidth);
    //      tilerow=-Math.floor((nw.y-Y0)/tilewidth);
    //      url = L.Util.template(this._url, {s: this._getSubdomain(tilePoint)});
    //      return url + L.Util.getParamString(this.wmtsParams, url) + "&tilematrix=" + ident + "&tilerow=" + tilerow +"&tilecol=" + tilecol ;
    //  },

    setParams: function(params, noRedraw) {
        L.extend(this.wmtsParams, params);
        if (!noRedraw) {
            this.redraw();
        }
        return this;
    },

    //  getDefaultMatrix : function () {
    //      /**
    //       * the matrix3857 represents the projection
    //       * for in the IGN WMTS for the google coordinates.
    //       */
    //      var matrixIds3857 = new Array(22);
    //      for (var i= 0; i<22; i++) {
    //          matrixIds3857[i]= {
    //              identifier    : "" + i,
    //              topLeftCorner : new L.LatLng(20037508.3428,-20037508.3428)
    //          };
    //      }
    //      return matrixIds3857;
    //  },
    getDefaultMatrix: function() {
        /**
         * the matrix3857 represents the projection
         * for in the IGN WMTS for the google coordinates.
         */
        var matrixIds4326 = new Array(22);
        for (var i = 0; i < 22; i++) {
            matrixIds4326[i] = {
                identifier: "" + i,
                topLeftCorner: new L.LatLng(90, -180)
            };
        }
        return matrixIds4326;
    },
    _isValidTile: function(coords) {
        var crs = this._map.options.crs;

        if (!crs.infinite) {
            // don't load tile if it's out of bounds and not wrapped
            var bounds = this._globalTileRange;
            if (this.options.noWrap && (coords.x < 0 || coords.x > bounds.max.x)) { return false }
            if ((!crs.wrapLng && (coords.x < bounds.min.x || coords.x > bounds.max.x)) ||
                (!crs.wrapLat && (coords.y < bounds.min.y || coords.y > bounds.max.y))) { return false; }
        }

        if (!this.options.bounds) { return true; }

        // don't load tile if it doesn't intersect the bounds in options
        var tileBounds = this._tileCoordsToBounds(coords);
        return L.latLngBounds(this.options.bounds).overlaps(tileBounds);
    },
});

L.tileLayer.wmts = function(url, options) {
    return new L.TileLayer.WMTS(url, options);
};


