import { sysClient } from '@/clients/hprose-client'
import * as Types from './mutation-types'
import QueryParam from '@/models/business/query-param'

const state = {
    queryParam: new QueryParam()
}

const getters = {
    [Types.queryParam]: ({ queryParam }) => queryParam
}

const mutations = {
    [Types.SET_QUERYPARAM]: (state, queryParam) => state.queryParam = queryParam
}

const actions = {
    [Types.seeMapData]: (context, args) => {
        return sysClient.getData('seeMapData', args);
    },
    [Types.putMapData]: (context, args) => {
        return sysClient.getData('putMapData', args);
    },
    [Types.edittMapData]: (context, args) => {
        return sysClient.getData('edittMapData', args);
    },
    [Types.getRecords]: (context, args) => {
        // 数据查询
        return sysClient.getData('getMapDataList', args);
    },
    [Types.delData]: (context, args) => {
        //删除或批量删除
        return sysClient.getData('deleteMapData', args,true);
    },
    [Types.getCompanyData]: (context,args)=>sysClient.getData('getCompanyList', args, false),
    [Types.getLanguageList]: (context,args)=>sysClient.getData('getLanguage', args, false),
    [Types.getPeriodList]:(context,args)=>sysClient.getData('getPeriod', args, false),
    [Types.getRegionList]:(context,args)=>sysClient.getData('getRegion', args, false),
    [Types.signMapData]:(context,args)=>sysClient.getData('signMapData', args, false),
    [Types.publishData]:(context,args)=>sysClient.getData('PublishMapData',args,true),
    [Types.getEnclPath]:(context,args)=>sysClient.getData('getMapPath',args,false),
    [Types.exportData]:(context,args)=>sysClient.getData('ExportMapData',args,true),
    [Types.importData]:(context,args)=>sysClient.getData('ImportMapData',args,true),
}
export default {
    state,
    getters,
    mutations,
    actions
}





