<template>
    <div :class="style.layout">
        <app-header :showbanner="showbanner"></app-header>
          <banner v-show="showbanner"></banner>  
        <banner-next :showbanner="showbanner"></banner-next>
        <router-view></router-view>
    </div>
</template>
<script>
import style from '@/css/main.css'
import AppHeader from '#com/header'
import Banner from '#com/banner'
import BannerNext from '#com/bannernext'
export default {
    name: "main",
    data() {
        return {
            style,
            showbanner:true
        }
    },
    components: {
        AppHeader,
        Banner,
        BannerNext
    },
    methods: {
        autoSize(){
            this.widthbox=$(window).width();
            if(this.widthbox<=860){
              this.showbanner=false;
            }else{
                this.showbanner=true;
            }; 
        }
        
    },
    mounted() {
    this.autoSize();
      $(window).newresize(this.autoSize,1)
    }
}
</script>





