import * as TYPES from './mutation-types'
import {
	sysClient
} from '@/clients/hprose-client'
const state = {
	user: null,
	hasLogined:false
}

const getters = {
	[TYPES.user]: ({user}) => user,
	[TYPES.hasLogined]: ({hasLogined}) =>hasLogined
}

const mutations = {
	[TYPES.SET_USER]: (state, user) => state.user = user,
	[TYPES.SET_HASLOINGED]:(state,hasLogined)=>state.hasLogined=hasLogined
};

const actions = {
	//登录服务
	[TYPES.login]: (context, args) => sysClient.getData("login", args,true),

	//获取session
	[TYPES.getSession]: (context, args) => sysClient.getData("checkLogin", args,true),

	//注销session
	[TYPES.exitSession]: (context,args) =>sysClient.getData("logout", args,true),
};

export default {
	state,
	mutations,
	getters,
	actions
};


