let install = function (Vue, options) {
	Vue.prototype.transDateStr2normal = function (dateStr) {
		if (!dateStr) return '';
		return dateStr.replace('T', ' ');
	}

	Vue.prototype.transDate2DatetimeStr = function (date) {
		if (!date || !(date instanceof Date)) return '';
		let year = date.getFullYear();
		let month = date.getMonth() + 1;
		let day = date.getDate();
		let hour = date.getHours();
		let minute = date.getMinutes();
		let second = date.getSeconds();
		return `${year}-${month}-${day} ${hour}:${minute}:${second}`;
	}
	Vue.prototype.formatDegree = function (value) {
		console.log(20001, value);
		if (!value) return;
		value = Math.abs(value);

		console.log(20007, value);
		let v1 = 0;
		let v2 = 0;
		let v3 = 0;
		if (value != 0) {
			v1 = Math.floor(value);//度 
			v2 = Math.floor((value - v1) * 60);//分  
			v3 = Math.round((value - v1) * 3600 % 60);//秒 
		}
		return v1 + '°' + v2 + '\′' + v3 + '″';
	}
	Vue.prototype.degreeConvertBack = function (value) {
		if (!value) return;
		var du = value.split("°")[0];
		var fen = value.split("°")[1].split("′")[0];
		var miao = value.split("°")[1].split("′")[1].split('″')[0];
		return Math.abs(du) + (Math.abs(fen) / 60 + Math.abs(miao) / 3600);
	}
	//坐标转换将度分秒转换成正负形式
	Vue.prototype.covertDegree = function (value, tuid) {
		let numberTuid = '';

		if (!tuid || !value) return;
		switch (tuid) {
			case 'N':
				numberTuid = this.degreeConvertBack(value);
				break;
			case "S":
				numberTuid = -this.degreeConvertBack(value);
				break;
			case "E":
				numberTuid = this.degreeConvertBack(value);
				break;
			case "W":
				numberTuid = -this.degreeConvertBack(value);
				break;
		}
		return numberTuid;
	}
	//坐标转换将正负转换成度分秒
	Vue.prototype.formatDegreeBack = function (value, tuid) {
		let numberTuid = '';
		if (value == 0)
			value = '0';
		if (!tuid || !value) return;

		if (tuid == "LON" || tuid == "lon") {
			if (Number(value) >= 0)
				numberTuid = this.formatDegree(value) + 'E';
			else
				numberTuid = this.formatDegree(value) + 'W';
		}
		else if (tuid == "LAT" || tuid == "lat") {
			if (Number(value) >= 0)
				numberTuid = this.formatDegree(value) + 'N';
			else 
				numberTuid = this.formatDegree(value) + 'S';
		}
		return numberTuid;
	}
	Vue.prototype.getNowFormatDate = function () {
		var date = new Date();
		var month = date.getMonth() + 1;
		var strDate = date.getDate();
		if (month >= 1 && month <= 9) {
			month = "0" + month;
		}
		if (strDate >= 0 && strDate <= 9) {
			strDate = "0" + strDate;
		}
		var currentdate = date.getFullYear() + month + strDate
		return currentdate;
	}
}
export default {
	install,
}



