// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'leaflet'
import 'leaflet/dist/leaflet.css'
import Vue from 'vue'
import router from '@/router'
import store from '@/vuex'
import '@/css/common.css'
// import './assese/font-awesome/css/font-awesome.min.css'
import 'element-ui/lib/theme-default/index.css'
import '@/css/element.css'
import '@/css/font/iconfont.css'
import Element from 'element-ui'
import PrototypeExtend from '@/plugins/vue-prototypes'
import App from '#/app'
import '@/util/array-find-polyfill'
import '@/util/jquery-shim'
import '@/util/tdtlayer'
import '@/libs/jquery.newresize.js'

import routerIntercept from '@/router/router-intercept'
Vue.use(PrototypeExtend);
Vue.use(Element);
routerIntercept(router,store);

Vue.config.productionTip = false;
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



