<template>
  <div :class="style.back">
    <div :class="style.title">
      <h2>
        WOWGOO Is ............
      </h2>
      <p>
        分享快乐，吐槽忧伤，闲言碎语磨叽生活
        记录生命中所有的遇见，转角欣赏美丽风光，空杯的装饰回忆的色彩
      </p>
    </div>
    <div :class="style.roundbac">
        <div v-for="v in bannerdata" :class="style.roundbox">
          <div :class="style.oneround" :id="'round'+v.id">
            <ul :class="style.font">
              <li :class="style.minifont">{{v.content}}</li>
              <li :class="style.minititle">{{v.name}}</li>
            </ul>
          </div>
        </div>
    </div>    
  </div>
</template>
<script>
import style from '@/css/banner.css'
// import CanvasRound from '../../../../static/libs/canvas-round.js'
// import headerbanner from '@/data/header-banner.json'
import { SET_HASLOINGED } from '../modules/login/mutation-types'
import { mapMutations, mapActions,mapGetters } from 'vuex'
import { exitSession, user } from '@/components/modules/login/mutation-types'

export default {
  name: 'banner',
  data() {
    return {
      style,
      isbanner:false,
      username: '超级用户',
      date: new Date(),
      isbanner:false,
      bannerdata:[
        {name:'web | 前端',id:'0',content:'web FED.....'},
        {name:'UI | 界面',id:'1',content:'User Interface.....'},
        {name:'FONT | 资源',id:'2',content:'font sourse.....'},
        {name:'API | 接口',id:'3',content:'UThe American Petroleum Institute .....'},
        {name:'UX｜UED ',id:'4',content:'User Experience.....'},
        {name:'COLOR | 配色',id:'5',content:'color sourse.....'}
      ],
      // bannerdata:headerbanner,
      hidebanner:true,
      widthbox:'',
      mini:true,
      active:'',
      canvas:{
            vx: 4,  //小球x轴速度,正为右，负为左
            vy: 4,  //小球y轴速度
            height: 2,  //小球高宽，其实为正方形，所以不宜太大
            width: 2,
            count: 500,   //点个数
            color: "121, 162, 185",   //点颜色
            stroke: "130,255,255",    //线条颜色
            dist: 6000,   //点吸附距离
            e_dist: 20000,  //鼠标吸附加速距离
            max_conn: 10 //点到点最大连接数
           }
      
    }
  },
  computed: {
    dateStr() {
      let year = this.date.getFullYear();
      let month = this.date.getMonth() + 1;
      let day = this.date.getDate();
      let hour = this.date.getHours();
      let minitue = this.date.getMinutes();
      let second = this.date.getSeconds();
      if (month < 10) {
        month = "0" + month;
      }
      if (day < 10) {
        day = "0" + day;
      }
      if (hour < 10) {
        hour = "0" + hour;
      }
      if (minitue < 10) {
        minitue = "0" + minitue;
      }
      if (second < 10) {
        second = "0" + second;
      }
      return `${year}-${month}-${day} ${hour}:${minitue}:${second}`
    },
    ...mapGetters({
      user
    })
  },
  methods: {
    ...mapActions({
      exitSession
    }),
    ...mapMutations({
      SET_HASLOINGED
    }),
      showHeader(){
          this.hidebanner=false;
          this.isbanner=false;
          this.mini=!this.mini 
    },
    showBanner(id){  //显示二级导航
      this.isbanner=true;
      this.active=id;

    },
    exit() {
      this.exitSession();
      this.SET_HASLOINGED(false);
      this.$router.push({ name: "login" });
    }, 

  },

  created() {
 
  },
  mounted() {

  }
}
</script>


